.card-image {
  max-height: 350px;
  min-height: 350px;
  object-fit: cover; }

.card {
  min-height: 525px; }

.list-group {
  max-width: 250px;
  max-height: 500px;
  overflow: hidden auto; }

.cursor-pointer {
  cursor: pointer; }

button:disabled {
  cursor: not-allowed; }

// mobile wish list
.star-container {
  margin: 0 auto;
  width: 50px;
  position: fixed;
  bottom: 50px;
  left: 50px;
  cursor: pointer; }

.star-container__img {
  width: 100%; }

.star-container__counter {
  min-width: 20px;
  width: fit-content;
  height: 20px;
  color: #fff;
  background-color: red;
  border-radius: 50%;
  position: absolute;
  top: -5%;
  left: 70%;
  font-size: 14px;
  border: 2px solid red;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center; }

.wish-list__popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #00000099;
  display: flex;
  justify-content: center;
  align-items: center; }
.popup__block {
  position: relative;
  width: 270px;
  height: 50%;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  & .block__close {
    width: 20px;
    position: absolute;
    top: -30px;
    right: 0;
    cursor: pointer; } }

// mobile wish list

@media (max-width: 767px) {
  .tab--lang {
    height: 100%; } }

@media (max-width: 575px) {
  .card {
    max-width: 300px;
    margin: 0 auto;
    height: auto; } }
